* {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

#nav {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 25px 0;
}

.nav-logo {
  font-size: 22px;
  font-weight: bold;
  color: rgb(0, 1, 5);
  text-decoration: none;
}

.nav-logo:hover {
  color: rgb(134, 134, 134);
}

#nav-links {
  margin-left: 72px;
  display: flex;
  gap: 24px;
  align-items: center;
}

#cart-link,
.nav-link {
  font-size: 12px;
  font-weight: bold;
  color: rgb(134, 134, 134);
  text-decoration: none;
  transition-duration: 0.3s;
  cursor: poiner;
}

.nav-link:hover {
  color: #1d1d1d;
}

#cart-link {
  display: flex;
  gap: 10px;
  margin-left: 24px;
  align-items: center;
  height: fit-content;
}

#cart-link:hover {
  color: #1d1d1d;
}

#cart-link > img {
  height: 32px;
  width: 32px;
}

#cart-counter {
  color: rgb(248, 248, 248);
  background: rgb(134, 134, 134);
  border: none;
  border-radius: 50%;
  height: 16px;
  width: 16px;
  text-align: center;
  transition-duration: 0.3s;
}

#cart-link:hover #cart-counter {
  background: #1d1d1d;
}

.section {
  width: 70%;
  margin: 0 auto;
}

#home {
  background: rgb(0, 1, 5);
  color: rgb(248, 248, 248);
  height: 60vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.subheader {
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 2px;
  text-align: center;
}

.home-subheader {
  margin: 1vh 0;
}

.home-header {
  font-size: 45px;
  letter-spacing: 2px;
  font-weight: 100;
}

.home-info {
  margin: 1vh 0;
}

.home-products-btn {
  margin: 2vh 0 0;
  padding: 20px;
  color: rgb(0, 1, 5);
  background: rgb(248, 248, 248);
  font-size: 11px;
  letter-spacing: 2px;
  font-weight: bold;
}

#story-section {
  width: 50%;
  margin: 13vh auto;
  text-align: center;
}

.story-section-header {
  font-size: 25px;
}

.story-section-text {
  margin: 2vh 0;
  font-size: 16px;
  color: rgb(134, 134, 134);
}

#story-btn {
  background: transparent;
  color: rgb(146, 86, 26);
  padding: 5px;
  border: none;
  border-bottom: 1px solid rgb(255, 150, 46);
  font-size: 14px;
  cursor: pointer;
  transition-duration: 0.3s;
}

#story-btn:hover {
  color: rgb(145, 63, 15);
  border-bottom: 2px solid rgb(145, 63, 15);
}

#products-boards {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70%;
  margin: 0 auto;
}

.products-board {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.decor-lines {
  position: relative;
  text-align: center;
  color: rgb(134, 134, 134);
  font-size: 11px;
  font-weight: bold;
}

.decor-lines::before {
  content: " ";
  display: block;
  height: 1px;
  width: 30px;
  position: absolute;
  top: 50%;
  left: -40px;
  background: rgb(134, 134, 134);
}

.decor-lines::after {
  content: " ";
  height: 1px;
  width: 30px;
  background: rgb(134, 134, 134);
  display: block;
  position: absolute;
  top: 50%;
  right: -40px;
}

.products-board-grid-featured {
  display: grid;
  grid-template-columns: 450px 450px;
  margin: 10vh 0;
}

.products-board-grid-featured > .mug {
  margin: 0 10px;
}

.products-board-grid {
  display: grid;
  grid-template-columns: 300px 300px 300px;
  gap: 15px;
  margin: 10vh 0;
}

.mug {
  aspect-ratio: 3/4;
}

.mug-img {
  width: 100%;
  aspect-ratio: 2/2.5;
  background: red;
}

.mug-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 3vh 0;
}

.mug-info-header {
  font-size: 20px;
  font-weight: 500;
}

.mug-info-price {
  margin: 1vh 0;
  color: rgb(134, 134, 134);
  display: flex;
  align-items: center;
  height: fit-content;
}

.sale-mug {
  position: relative;
}

.sale-mug::after {
  content: "On Sale";
  height: fit-content;
  width: fit-content;
  background: rgb(248, 248, 248);
  color: rgb(145, 63, 15);
  display: block;
  position: absolute;
  top: 2%;
  right: 2%;
  padding: 10px 15px;
  font-size: 16px;
  font-weight: bold;
}

.mug-price-new {
  font-size: 20px;
  color: rgb(190, 31, 3);
  font-weight: bold;
}

.mug-price-old {
  font-size: 16px;
  position: relative;
  margin: 0 10px;
}

.mug-price-old::before {
  content: " ";
  height: 1px;
  width: 110%;
  position: absolute;
  top: 50%;
  left: -5%;
  background: rgb(107, 107, 107);
  display: block;
}

.offer-container > .subheader {
  color: rgb(134, 134, 134);
}

.offer {
  margin: 10vh 0;
  display: flex;
  gap: 10px;
  height: fit-content;
  width: 800px;
}

.offer-info {
  text-align: left;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 2vh 0;
}

.offer-subheader {
  text-align: start;
  color: rgb(134, 134, 134);
  font-size: 12px;
}

.offer-header {
  font-size: 40px;
  font-weight: 300;
  margin: 1vh 0;
  color: #1d1d1d;
}

.offer-desctip {
  letter-spacing: 2px;
  color: rgb(134, 134, 134);
  margin: 2vh 0;
  font-size: 13px;
  width: 85%;
}

.offer-shopping-btn {
  background: rgb(0, 1, 5);
  color: rgb(248, 248, 248);
  padding: 15px 20px;
  text-decoration: none;
  font-size: 11px;
  letter-spacing: 2px;
  font-weight: bold;
  height: fit-content;
  width: fit-content;
}

.offer-gallery {
  width: 50%;
  aspect-ratio: 3/2;
  background: peru;
}

.offer-container > .subheader {
  width: fit-content;
  margin: 0 auto;
}

